/**
 * http
 */
// axioselement uiloadingmessage

import axios from 'axios'
import { Loading, Message } from 'element-ui'

axios.defaults.timeout=50000;
// http
var loadinginstace;
axios.interceptors.request.use(config=>{
  //element ui Loading

     console.log('-------------------request init----------------');

  loadinginstace=Loading.service({
    lock: true,
    text: '...',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0.7)'
  });
  return config
},error => {
   loadinginstace.close();
  Message.error({
    message:''
  });
  return Promise.reject(error)
});
axios.interceptors.response.use(data=>{//loading
  console.log('-------------------response init----------------');
  loadinginstace.close();

  if (data.data && data.data.errcode) {
    /*if (parseInt(data.data.errcode) === 40001) {
      //
      console.log("");
      //this.$root.Bus.$emit('goto', '/login')
    }else{
      console.log("");

    }*/
  }

  return data
},error => {
   loadinginstace.close();
  Message.error({
    message:''
  });
  return Promise.reject(error)
});
//
let base = '';  //config/index.jsproxyTable

//
export const POST = (url, params) => {
  return axios.post(`${base}${url}`, params).then(res => res.data)
}

export const GET = (url, params) => {
  return axios.get(`${base}${url}`, {params: params}).then(res => res.data)
}

export const PUT = (url, params) => {
  return axios.put(`${base}${url}`, params).then(res => res.data)
}

export const DELETE = (url, params) => {
  return axios.delete(`${base}${url}`, {params: params}).then(res => res.data)
}

export const PATCH = (url, params) => {
  return axios.patch(`${base}${url}`, params).then(res => res.data)
}




