<template>
  <div class="eqlist">
            <el-row>
              <el-col :span="24" style="">
                <span style="font-weight: bold;">SMT1</span>
                <dl>
                  <dt style="padding: 5px 0;font-size:12px;"><i class="el-icon-star-on"></i><span>DEK+GXH-3HITACHI +GXH-1JS HITACHI  +HOTFLW-9CR ()+ +AOI </span></dt>
                </dl>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24" style="">
                <span style="font-weight: bold;">SMT 2</span>
                <dl>
                  <dt style="padding: 5px 0;font-size:12px" ><i class="el-icon-star-on"></i><span>DEK+GXH-3 HITACHI  +JUKI 760JUKI +HOTFLW-9CR ()+ +AOI </span></dt>
                </dl>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24" style="">
                <span style="font-weight: bold;">SMT 3</span>
                <dl>
                  <dt style="padding: 5px 0;font-size:12px" ><i class="el-icon-star-on"></i><span>DEK+GXH-3 HITACHI  +GXH-1JS(HITACHI)+HOTFLW-9CR ()+ +AOI )</span></dt>
                </dl>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24" style="">
                <span style="font-weight: bold;">DIP 1/2</span>
                <dl>
                  <dt style="padding: 5px 0;font-size:12px" ><i class="el-icon-star-on"></i><span>12m++16m+B/I+16m</span></dt>
                </dl>
              </el-col>
            </el-row>
  </div>
</template>

<script>
    export default {
        name: "gongsijieshao"
    }
</script>

<style scoped>
 .eqlist{
   padding: 20px;
 }
 .eqlist .el-icon-star-on{
    padding-right: 10px;
  }
</style>



