import * as API from './'

export default {


  //
  login: params => {
    return API.POST('/api/v1/accounts/login', params)
  },

  //
  register: params => {
    return API.POST('/api/v1/accounts/register', params)
  },
  //
  changePwd: params => {
    return API.POST('/api/v1/accounts/changePwd', params)
  },


}



