<template>
  <div class="products-search">
    <el-row>
      <el-select size="small" v-model="valuews" placeholder="">
        <el-option
          v-for="item in optionsws"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-row>
    <el-row>
      <el-select disabled="" size="small" v-model="valuetype" placeholder="">
        <el-option
          v-for="item in optionstype"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-row>
    <el-row>
        <el-input size="small" v-model="input" placeholder=""></el-input>
    </el-row>
    <el-row>
        <el-button style="width: 100%" size="small" type="primary" @click="searchProductsBtn()"></el-button>
    </el-row>
    <el-row>
      <el-tree
        style="height: 100%;"
        :data="treedata"
        :props="defaultProps"
        accordion
        @node-click="handleNodeClick">
      </el-tree>
    </el-row>
  </div>
</template>

<script>
  import API from '../../api/api_products';
    export default {
      name: "serach",
      data() {
        return {
          treedata:[
            {
              label: '/3C',
              children: [{
                label: ''
              }, {
                  label: '',
              }, {
                  label: 'USB',
              }, {
                  label: '',
                children: [{
                  label: 'CPU'
                },{
                  label: ''
                }]
              }]
            }, {
              label: '',
              children: [{
                label: '',
              }, {
                label: '',
              }, {
                label: '',
                children: [{
                  label: ''
                }]
              }]
            }, {
              label: '',
              children: [{
                label: '/',
                children: [{
                  label: ''
                },{
                  label: ''
                }]
              }, {
                label: '',
                children: [{
                  label: ''
                }]
              }, {
                label: '',
                children: [{
                  label: ''
                },{
                  label: '&'
                }]
              }]
            }, {
              label: '',
              children: [{
                label: '',
                children: [{
                  label: ''
                },{
                  label: ''
                },{
                  label: 'LED'
                },{
                  label: ''
                },{
                  label: ''
                },{
                  label: ''
                },{
                  label: ''
                },{
                  label: ''
                }]
              },{
                label: '',
                children: [{
                  label: ''
                }]
              }]
            }, {
              label: '',
              children: [{
                label: '',
                children: [{
                  label: 'ASL'
                },{
                  label: 'uASL'
                },{
                  label: 'iDL'
                },{
                  label: 'G7'
                },{
                  label: 'uDL'
                }]
              },{
                label: '',
              }]
            },
          ],defaultProps: {
            children: 'children',
            label: 'label'
          },
          input: '',
          valuews:'',
          valuetype:'',
          optionsws:[
            {
              value: '0,2500',
              label: ''
            },
            {
              value: '0,5',
              label: '<5W'
            }, {
              value: '5,10',
              label: '5~10W'
            }, {
              value: '11,20',
              label: '11~20W'
            }, {
              value: '21,50',
              label: '21~50W'
            }, {
              value: '50,100',
              label: '51~100W'
            }, {
              value: '101,300',
              label: '101~300W'
            }, {
              value: '301,500',
              label: '301~500W'
            }, {
              value: '501,1000',
              label: '501~1000W'
            }, {
              value: '1000,2500',
              label: '10KVA Max.'
            }, {
              value: '2500',
              label: '2500KVA Max.'
            }
          ],
          optionstype:[
            {
              value: '1',
              label: 'Charger'
            }, {
              value: '2',
              label: 'Cordless Phone'
            }, {
              value: '3',
              label: 'Home Appliance Power'
            }, {
              value: '4',
              label: 'Industrial Power'
            }, {
              value: '5',
              label: 'Lighting'
            }, {
              value: '6',
              label: 'Network Communication'
            }, {
              value: '7',
              label: 'OEM'
            }, {
              value: '8',
              label: 'Power Conversion'
            }, {
              value: '9',
              label: 'Variable Frequency Driver'
            }, {
              value: '10',
              label: 'USB PD'
            }, {
              value: '11',
              label: 'Others'
            }
          ],
          productsList:[]
        }
      },
      methods: {
        handleClick() {
          alert('button click');
        },
        handleNodeClick(data) {
          console.log(data);
        },
        searchProductsBtn(){
          let that = this;
          this.$store.commit('productListOrDetailPageStore',0);//
          let ws=that.valuews,type=that.valuetype,inp=that.input;
          let arr=[],gte="",lte="";
          if(ws!=""){
            arr=ws.split(",");
           if(arr.length==1){
                gte=arr[0];
            }else if(arr.length==2){
                gte=arr[0];
                lte=arr[1];
              }
          }else {
            gte=0;
            lte=2500;
          }
          //"lt","gte","lte"
          let params={gte:Number(gte),lte:Number(lte),inp:inp};
          API.findProductsListByParams(params).then(function (result) {
            that.loading = false;
            if(result){
              that.$store.commit('ProductsListStore',result.result.list);
            } else {
              that.$message({showClose: true,type: result.type, message: result.errmsg || '', duration: 2000});
            }
          }, function (err) {
            that.loading = false;
            that.$message({showClose: true, type: err.type, message: err.toString(), duration: 2000});
          }).catch(function (error) {
            that.loading = false;
            that.$message({showClose: true,type: 'error', message: '', duration: 2000});
          });
        }
      }
    }
</script>

<style scoped>
  .products-search{
    margin: 20px;

  }
  .products-search .el-row{
    margin: 10px 0;
  }
  .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
</style>



