import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
const state={   //state
  showFooter: true,
  changableNum:0,
  productListOrDetailPage:0, //"0"  "1"
  productsList:{},
  productsDetails:{}
  //
};
const getters = {   //state()
  isShow(state) {  //showFooter
    return state.showFooter
  },
  getChangedNum(){  //changebleNum
    return state.changableNum
  }
};
const mutations = {
  show(state) {   //statestate();
    state.showFooter = true;
  },
  hide(state) {  //
    state.showFooter = false;
  },
  newNum(state,sum){ //statesum
    state.changableNum+=sum;
  },
  productListOrDetailPageStore(state,ststus){
    state.productListOrDetailPage=ststus;
  },
  ProductsDetailsStore(state,productsDetails){
    state.productsDetails=productsDetails;
  },
  ProductsListStore(state,productsList){
    state.productsList=productsList;
  }
};
const actions = {
  hideFooter(context) {  //mutationscontextstore 
    context.commit('hide');
  },
  showFooter(context) {  //
    context.commit('show');
  },
  getNewNum(context,num){   //num
    context.commit('newNum',num)
  }
};
const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions
});
export default store;



