import * as API from './'

export default {

  //
  findList: params => {
    return API.GET('/api/v1/products/findAllProductsList', params)
  },
  //
  findProductsListByParams: params => {
    return API.POST('/api/v1/products/findProductsListByParams', params)
  },
}



